import React from 'react';

//  HEADER ELEMENTS -------------------------------------
    // Org -----------------------------------------------
    export const OrgHeader = (_clientData,_serverData) => (
        <div> {_serverData.CompName},   
             {_serverData.FIO}, 
               ,   
            ,    {""}
             {_clientData.compFullName},  
              {_clientData.
            FIO}, ()   ,   
             ,
        </div>
     );
    // IP ---------------------------------------------
    export const IPHeader = (_clientData,_serverData) => (
        <div>  {_serverData.CompName},     {_serverData.FIO},
               ,   ,  
             ""  {_clientData.Name},   
             {_clientData.FIO}, ()  
            ,    ,
        </div>
    );
    // FL
  export const FLHeader = (_clientData,_serverData) => (
    <div> {_serverData.CompName},   
      {_serverData.FIO},     , 
        ,    "" 
      {_clientData.lastName} {_clientData.firstName}
      {_clientData.midName}, {_clientData.lastName} 
      {_clientData.Serial}  {_clientData.number}, 
      {_clientData.whoGave} {_clientData.whenGave} 
       {_clientData.codeGave}, () 
       ,
    </div>
  );
    

